@font-face {
  font-family: "filmP3";
  src: url("../fonts/filmP3.eot");
  src: url("../fonts/filmP3.eot") format("embedded-opentype"),
    url("../fonts/filmP3.woff2") format("woff2"),
    url("../fonts/filmP3.woff") format("woff"),
    url("../fonts/filmP3.ttf") format("truetype"),
    url("../fonts/filmP3.svg#filmP3") format("svg");
  font-weight: 700;
}

